.home-container{

  min-height: 400px;
  display: flex;
  flex-direction: row;
  padding: 0 32px;

  > .left{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 2rem;
    flex: 1;

    > a{
      background: #EC2680;
      padding: 12px 56px;
      border: none;
      border-radius: 9999px;
      text-decoration: none;
      color: #fff;
      transition: 0.2s;

      &:hover{
        filter: saturate(1.2);
      }
    }
    

    h2{
      span{
        color: #5191D4;
        font-weight: 800;
      }
      width: 30ch;
      justify-self: center;
    }
  }

  > .right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 32px 32px 32px 0;

    img{
      user-select: none;
      
    }
  }
}